<template>
    <div class="tab-row">
       <div  @click="idx = 0" class="tab-item item-icon" :class="this.idx == 0 ? 'active' : ''">
           <!-- <div @click="goModule('http://zhasdemo.huichifangqiu.com/')">
                <img :src="this.idx == 0? this.shouye_act : this.shouye" alt="" >
                <p>首页</p>
           </div> -->
           <div>
               <img :src="this.idx == 0? this.shouye_act : this.shouye" alt="" >
                <p>首页</p>
           </div>
       </div>
       <div  @click="idx = 1" class="tab-item item-icon" :class="this.idx == 1 ? 'active' : ''">
           <div @click="goModule('http://wxjzdemo.zhas.huichifangqiu.com')">
               <img :src="this.idx == 1? this.wxjuzheng_act : this.wxjuzheng" alt="" >
               <p>微信矩阵</p>
           </div>
       </div>
       <div class="tab-item tab-aias">
           <img :src="this.aias" alt="">
       </div>
       <div @click="idx = 3" class="tab-item item-icon" :class="this.idx == 3 ? 'active' : ''">
           <div @click="goModule('http://cshddemo.zhas.huichifangqiu.com')">
                <img :src="this.idx == 3? this.cshuodong_act : this.cshuodong" alt="" >
                <p>城市活动</p>
           </div>
       </div>
       <div @click="idx = 4" class="tab-item item-icon" :class="this.idx == 4 ? 'active' : ''">
           <div @click="goModule('http://medemo.zhas.huichifangqiu.com')">
                <img :src="this.idx == 4? this.wode_act : this.wode" alt="" >
                <p>我的</p>
           </div>
       </div>
    </div>
</template>
<script>
import shouye from "../assets/image/shouye.png"
import shouye_act from "../assets/image/shouye_act.png"
import wxjuzheng from "../assets/image/wxjuzheng.png"
import wxjuzheng_act from "../assets/image/wxjuzheng_act.png"
import cshuodong from "../assets/image/cshuodong.png"
import cshuodong_act from "../assets/image/cshuodong_act.png"
import wode from "../assets/image/wode.png"
import wode_act from "../assets/image/wode_act.png"
import aias from "../assets/image/aias.png"
export default {
    data () {
        return {
            idx: 0,
            shouye: shouye,
            shouye_act: shouye_act,
            wxjuzheng: wxjuzheng,
            wxjuzheng_act: wxjuzheng_act,
            cshuodong: cshuodong,
            cshuodong_act: cshuodong_act,
            wode: wode,
            wode_act: wode_act,
            aias: aias
        }
    },
    methods: {
        //跳转到其他模块链接
        goModule(url){
            location.href = url;
        },
    }
}
</script>

<style lang="scss" scoped>
    .tab-row{
        width: 100%;
        height: 1.58rem;
        background-size: 100% 1.58rem;
        background-repeat: no-repeat;
        background-position: center;
        background-image: url(../assets/image/tabbg.png);
        position: fixed;
        bottom: 0;
        left: 0;
        display: flex;
        justify-content: space-around;
        .tab-item{
            width: 1.32rem;
            text-align: center;
            font-size: 0.22rem;
            color: #666666;
        }
        .item-icon{
            padding-top: 0.68rem;
            img{
                width: 0.58rem;
                height: 0.58rem;
            }
        }
        .active{
            color:#1296DB;
        }
        .tab-aias{
            padding-top: 0.16rem;
            img{
                width: 1.32rem;
                height: 1.32rem;
            }
        }
    }
</style>


