import axios from 'axios';
import qs from 'qs';

const api = axios.create({
    baseURL: 'http://zhas.huichifangqiu.com/home/api',
    withCredentials: true,
    headers: {
        post: {
            // 'Content-Type': 'application/x-www-form-urlencodedl'
        }
    }
});

const fetch = (path, data) => {
    return new Promise((resolve, reject) => {
        return api({
            method: 'POST',
            data: qs.stringify(data),
            url: path
        }).then(
            ({ status, data }) => {
                if (status === 200 && data) {
                    switch (data.code) {
                        case -101:
                            location.href = data.data;
                            break;
                        case 0:
                            resolve(data); //(data.data)
                            break;
                        default:
                            reject(data);
                            break;
                    }
                } else {
                    reject(data);
                }
            },
            reject
        );
    });
};

// ========== 用户 ========== //
// 登录信息
export function Login(lon, lat) {
    return fetch('getMeinfo', {
        "lon": lon,
        "lat": lat
    });
}
// bannner 广告
export function bannerList() {
    return fetch('getBannerList');
}

// 市民心声
export function heartfel() {
    return fetch('peopleWish');
}

//SDK配置
export function SDKconfig() {
    return fetch('getJsConfig');
}


